<template >
    <div class="container">
        <h5>
          <span>{{ Cname }}</span>
          <span>{{ right }}</span>
          <span>{{ record }}</span>
        </h5>
    </div>
</template>
<script>
export default {
  name: 'MyFooter',
  data () {
    return {
      Cname: '',
      right: '2016',
      record: 'ICP11031566-1'
    }
  }
}
</script>
<style scoped>
.container{
        max-width: 1200px;
        margin: 0 auto;
        background-color: rgb(132, 134, 132);
        height: 32px;
        line-height: 32px;
    }
h5{
     color: white;
}
</style>



